import React, { Component } from 'react';
import './ProjectsView.css';
import SkillUse from './skillUse/SkillUse';

import arrow from './arrow.png';

export default class ProjectsView extends Component {
  constructor(){
    super();
    this.state={
      saphira:{
        imgSrc: ['./img/SaphiraApp.png','./img/ProjectJoel.png'],
        desc: 'Aliquip excepteur amet fugiat qui sint aliquip cupidatat duis quis ipsum ex cupidatat. Reprehenderit nisi mollit tempor deserunt aute dolore ex. Excepteur non et labore proident nostrud eu quis. Consequat tempor ipsum fugiat commodo sit. Sint culpa ea excepteur exercitation non est. Adipisicing sunt fugiat Lorem deserunt deserunt aute. Quis ipsum aliquip consectetur officia.',
        skillUse: ['React','HTML','CSS']
      },
      joel:{
        imgSrc: ['./img/ProjectJoel.png','./img/SaphiraApp.png'],
        desc: 'hola',
        skillUse: ['React','HTML','CSS']
      },
      current: 0,
      isNext: true
    }
    this.handlerPrev = this.handlerPrev.bind(this);
    this.handlerNext = this.handlerNext.bind(this);
  }
  handlerPrev() {
    let index = this.state.current,
        length = this.state.saphira.imgSrc.length;
    
    if( index < 1 ) {
      index = length;
    }
    
    index = index - 1;
  
    this.setState({
      current: index,
      isNext: false
    });
  }
  
  handlerNext() {
    let index = this.state.current,
        length = this.state.saphira.imgSrc.length - 1;
    
    if( index === length ) {
      index = -1;
    }
    
    index = index + 1;
    
    this.setState({
      current: index,
      isNext: true
    });
  }


  render(props) {
    let index = this.state.current,
        imgSrc = this.state.joel.imgSrc[index],
        skillArray = this.state.saphira.skillUse;
    return (
      <div className='ProjectsView'>
        <div className='ProjectImage'>
           <img src={imgSrc}/>
           <div id='left' onClick={this.handlerPrev}><img src={arrow}/></div>
           <div id='right' onClick={this.handlerNext}><img src={arrow}/></div>
           <div onClick={this.props.closeView} id='close-view'><span>X</span></div>
        </div>
        <div className='ProjectDesc'>
            <h1>{this.props.projectName}</h1>
            <p>{this.state.saphira.desc}</p>
            <div className='skillsUseProject'>
              {skillArray.map(
                (skill,key) =>{
                    return(
                     <SkillUse skillUse={skill} key={key}/> 
                    )
                }
              )}
            </div>
        </div>
      </div>
    )
  }
}
