import React from 'react';
import './Rank.css';
import SkillComponent from './SkillComponent';


const Rank = ()=>{
    
    return(
        <div className='Rank'>
            <SkillComponent skill='JS' percentageSkill={85}/>
            <SkillComponent skill='REACT' percentageSkill={70}/>
            <SkillComponent skill='UX' percentageSkill={40}/>
            <SkillComponent skill='PSD' percentageSkill={55}/>
            <SkillComponent skill='NODEJS' percentageSkill={40}/>
            <SkillComponent skill='EXPRESS' percentageSkill={35}/>
            <SkillComponent skill='MKTDG' percentageSkill={40}/>
        </div>
    )
}

export default Rank;