import React from 'react';
import './Menu.css';
import { HashLink as Link } from 'react-router-hash-link';

import wolf from './wolf.png';
import menu from './menu.png';
 
class Menu extends React.Component{
    constructor(){
        super()
        this.state = {
          displayMenu: false,
        };
        this.showMenu = this.showMenu.bind(this);
      }
    showMenu(){
        this.setState({
           displayMenu: !this.state.displayMenu
          });
       }
    render(){
        const { displayMenu } = this.state;
        return(
            <div>
                <div className='menu-icon' id='menu' onClick={this.showMenu}>
                    <img alt='menu' src={menu}></img>
                </div>
                <div className={displayMenu ? "Menu Menu-open" : "Menu"} id='Menu'>
                    <Link to='/' onClick={this.showMenu}><img alt='wolf' src={wolf}></img></Link>
                    <ul>
                        <li><Link to='/#Skills' onClick={this.showMenu}>WHO I AM?</Link></li>
                        <li><Link to='/#Projects' onClick={this.showMenu}>PROJECTS</Link></li>
                        <li><Link to='/blog'  onClick={this.showMenu}>BLOG</Link></li>
                        <li><Link to='/#Footer' onClick={this.showMenu}>CONTACT</Link></li>
                    </ul>
                </div>
            </div>
        )
    }
}
export default Menu;