import React from 'react';
import './Recent.css';

import { Link } from "react-router-dom";

const Recent = (props) =>{
    return(<div id='recent-blog'>
        <h4>Recent</h4>
        <img src={props.img||require('./imgR.png')}/>
        <h1>{props.title||'PROIDENT MAGNA LABORE ALIQUIP IPSUM LABORE.'}</h1>
        <p>{props.description||'Pariatur tempor voluptate dolor ipsum consequat irure officia exercitation anim sint ea dolore consequat..'}</p>
        <div id='readMore'><Link to='/article'>Read More..</Link></div>
    </div>);
}
export default Recent;