import React from 'react';
import './ListBlog.css';
import ArticleView from './ArticleView';
import articles from './articles.json';

const ListBlog = ()=>{
    console.log(articles);
    return(
        <div id='list-blog'>
            <h2>Anteriores</h2>
            {articles.map(a => 
                <ArticleView
                link={a.link}
                imgSrc={a.imgSrc}
                section={a.section}
                title={a.title}
                desc={a.desc}
                />
                )}            
        </div>
    )
}
export default ListBlog;