import React from 'react';
import './ListBlog.css';
import { Link } from "react-router-dom";

const ArticleView = (props)=>{
    return(
        <div id='ArticleView'><Link to={props.link}>
            <img src={props.imgSrc}/>
            <h4>{props.section}</h4>
            <h1>{props.title}</h1>
            <p>{props.desc}</p>
        </Link>
        </div>
    )
}
export default ArticleView;