import React from 'react';

import './Blog.css';

import Menu from '../Menu/Menu';        
import Recent from './Recent/Recent';
import ListBlog from './ListBlog/ListBlog';

import { Link } from "react-router-dom";


const Blog = () =>{
    return(
        <>
        <Menu/>
        <Link to='/'><h1 id='title-blog'>FERNANDO OVIEDO</h1></Link>
        <Link to='/blog'><h1 id='title-blog-2'>BLOG</h1></Link>
        <div className='contain'>
            <Recent/> {/* agregar component de recent */}
            <ListBlog/>
        </div>
        {/*<Article/> */}
        </>
    )
}
export default Blog;