import React from 'react';


const SkillComponent = (props)=>{
    const percentageSkillValue = {
        width: `${props.percentageSkill}%`,
    };
    const skillValue = <div className='skillValue' style={percentageSkillValue} key='key'></div>;
    return(
        <div className='skill-component'>
            <h1>{props.skill}</h1>
            <div className='skill-loader'>
                    <div></div>
                    {skillValue}            
            </div>
        </div>
    )
}

export default SkillComponent;
