import React, {Component} from 'react';
import './ProjectsCarousel.css';

import ProjectsView from '../../ProjectsView/ProjectsView';

class ProjectsCarousel extends Component{
    constructor(){
        super();
        this.state = {
            projects:[
                    {
                        projectName: 'SAPHIRA APP',
                        projectImg: './img/SaphiraApp.png',
                        id:'Saphira'
                    },
                    {
                        projectName: 'JOEL ALMEIDA',
                        projectImg: './img/JoelAlmeida.png',
                        id: 'Joel'
                    }],
            current: 0,
            isNext: true,
            projectsView: false,
        }
        this.handleNextCarousel = this.handleNextCarousel.bind(this);
        this.handlePrevCarousel = this.handlePrevCarousel.bind(this);
        this.handleProjectsView = this.handleProjectsView.bind(this)

    }
    handlePrevCarousel(){
        let index = this.state.current,
        length = this.state.projects.length;
    
        if( index < 1 ) {
        index = length;
        }
        
        index = index - 1;
    
        this.setState({
        current: index,
        isNext: false,
        });
    }
    handleNextCarousel(){
        let index = this.state.current,
        length = this.state.projects.length - 1;
    
        if( index === length ) {
        index = -1;
        }
        
        index = index + 1;
        
        this.setState({
        current: index,
        isNext: true
        });
    }
    handleProjectsView(){
        this.setState({
            projectsView: !this.state.projectsView
        })
    }
    render(){
        const { projectsView } = this.state;

        let index = this.state.current,
        src = this.state.projects[index].projectImg,
        name = this.state.projects[index].projectName,
        id = this.state.projects[index].id;
        return(
            <div className='ProjectsCarousel'>

                <div className='right-btn' onClick={this.handleNextCarousel}><img alt='arrow' src='./img/arrow.png'></img></div>
                <div className='left-btn'  onClick={this.handlePrevCarousel}><img alt='arrow' src='./img/arrow.png'></img></div>
                
                <div className='Slide'>
                    <h1>{name}</h1>
                    <img alt='project' src={src}></img>
                </div>
                <div onClick={this.handleProjectsView} className='View-btn'><a>View</a></div>
                {projectsView && <ProjectsView imgStock={id} projectName={name} closeView={this.handleProjectsView}/>}
            </div>
        )
    }
}

export default ProjectsCarousel;