import React from 'react';
import './Article.css';

import Menu from '../Menu/Menu';
import Footer from '../Footer/Footer';
import { Link } from "react-router-dom";

const Article1 = (props)=>{
    return(
        <div className='Article'>
            <Menu/>
            <Link to='/'><h1 id='title-blog'>FERNANDO OVIEDO</h1></Link>
            <Link to='/blog'><h1 id='title-blog-2'>BLOG</h1></Link>

            <h1 id='title-article'>¿CÓMO LLEVO MI PEQUEÑO NEGOCIO A LAS REDES?</h1>
            <h3 id='section'>EMPRENDIMIENTOS</h3>
            <h3 id='date'>1|07|2019</h3>
            <img id='imgTitle' src={props.imgTitle||'img/web-dev.png'}/>
            <p>
                En pleno 2019
            </p>

            <Footer/>
        </div>
    )
}


export default Article1;